<template>
	<div id="app">
		<div v-if="isAdminPage" class="admin-container">
			<AdminHeader />
			<router-view />
		</div>
		<div v-else class="main-container">
			<div class="header">
				<Header />
				<Slider v-if="getPath == '/'" />
				<Side-menu />
			</div>

			<router-view />

			<Footer />
		</div>
	</div>
</template>

<script>
import Header from '@/components/Header';
import AdminHeader from '@/components/AdminHeader';
import Slider from '@/components/Slider';
import SideMenu from '@/components/SideMenu';
import Footer from './components/Footer.vue';

export default {
	name: 'App',
	components: {
		Header,
		Slider,
		SideMenu,
		Footer,
		AdminHeader,
	},

	data: () => ({}),
	computed: {
		isAdminPage() {
			let array = this.$route.path.split('/');
			return array[1] == 'admin';
		},
		getPath() {
			return this.$route.path;
		},
	},
	methods: {
		goto(url) {
			this.$router.push(url);
		},
	},
};
</script>

