<template>
	<header class="header" id="header">
		<div class="header-left">
			<router-link to="/"><Logo class="logo"/></router-link>
			<ul class="nav-category">
				<li class="sub introduction">
					<span @click="goto('/')"></span>
					<ul class="sub-category introduction">
						<li @click="goto('/about')">
							<span></span>
						</li>
						<li @click="goto('/member')">
							<span></span>
						</li>
					</ul>
				</li>
				<li @click="goto('/program')"></li>
				<li @click="goto('/goods')"></li>
				<li class="sub contents">
					<span @click="goto('/youtube')"></span>
					<ul class="sub-category contents">
						<li @click="goto('/youtube')"></li>
						<li @click="goto('/column/1')"></li>
					</ul>
				</li>
				<!-- 	<li onclick="searchBtn()"></li> -->
			</ul>
		</div>

		<span class="header-right">
			<!-- <button class="btn small disable" @click="goto('/join/login')"></button> -->
			<div v-if="getToken">
				<div class="dropdown marR-10">
					<button class="btn small"> </button>
					<div class="dropdown-content">
						<router-link to="/admin/rolling-banner" v-if="getAdmin == 'Y'"
							></router-link
						>
						<router-link :to="{ name: 'MyPage', params: { id: getId } }"
							></router-link
						>
						<!-- <router-link to="/order"></router-link>
						<router-link to="/review"></router-link> -->
						<router-link :to="{ path: `/question/all/${createdId}/1` }"
							>1:1</router-link
						>
						<a @click="logout"></a>
					</div>
				</div>

				<!-- <button class="btn small" @click="goto('/cart')">
					<span></span>
					<span class="count">0</span>
				</button> -->
			</div>
			<button v-else class="btn small" @click="goto('/login')">
				
			</button>

			<span @click="clickMenu">
				<Hamburger class="icon menu" />
			</span>
		</span>
	</header>
</template>

<script>
import { mapGetters } from 'vuex';
import Logo from '@/assets/icon/Logo.vue';
import Hamburger from '@/assets/icon/Hamburger.vue';
export default {
	components: { Logo, Hamburger },
	computed: {
		...mapGetters([
			'getIsNavOpen',
			'getToken',
			'getAdmin',
			'getIdentity',
			'getId',
		]),
	},
	created() {
		this.createdId = this.getIdentity;
	},
	data() {
		return {
			isActive: false,
			createdId: '',
		};
	},

	methods: {
		clickMenu() {
			this.$store.commit('toggleNav');
		},
		goto(url) {
			this.$router.push({ path: url });
		},
		async logout() {
			await this.$store.dispatch('LOGOUT');
			this.$router.go();
		},
	},
};
</script>

<style></style>
