import { instance, instanceWithAuth } from './index';

function sendAuthNumber(data) {
	return instance.post(`api/userApi/sendAuthNumber`, data);
}

function sendAuthNumberForPassword(data) {
	return instance.post(`api/userApi/sendAuthNumberForPw`, data);
}

function checkAuthNumber(data) {
	return instance.post(`api/userApi/checkAuthNum`, data);
}

function sendOnetimePassword(data) {
	return instance.post(`api/userApi/onetimePw`, data);
}

function join(data) {
	return instance.post(`api/userApi/join`, data);
}

function login(data) {
	return instance.post(`api/userApi/login`, data);
}

function logout() {
	return instanceWithAuth.post(`api/user/logout`);
}

function fetchUserTotalCount() {
	return instance.get(`api/userApi/totalCount`);
}

function fetchUserList(page, grade) {
	return instance.get(`api/userApi/all/${page}/${grade}`);
}

function fetchUserDetail(id) {
	return instance.get(`api/userApi/detail/${id}`);
}

function updateUser(id, data) {
	return instanceWithAuth.put(`api/userApi/update/${id}`, data);
}

function updateGrade(data) {
	return instance.put(`api/userApi/updateGrade`, data);
}
function deleteUser(data) {
	return instance.delete(`api/admin/userApi/delete`, { data });
}
function signOut(id) {
	return instance.delete(`api/userApi/delete/${id}`);
}
function checkPasswordAgain(data) {
	return instance.post(`api/userApi/checkPwAgain`, data);
}

export {
	fetchUserTotalCount,
	sendAuthNumber,
	checkAuthNumber,
	sendOnetimePassword,
	sendAuthNumberForPassword,
	join,
	login,
	logout,
	fetchUserList,
	fetchUserDetail,
	updateUser,
	updateGrade,
	signOut,
	deleteUser,
	checkPasswordAgain,
};
