'use strict';
// import USER from '@/api/user';
import {
	saveAuthToCookie,
	saveAdminToCookie,
	saveIdToCookie,
	saveIdentityToCookie,
	getAuthFromCookie,
	getAdminFromCookie,
	getUserFromCookie,
	getIdFromCookie,
	getIdentityFromCookie,
	deleteCookie,
} from '@/utils/cookies';
import { login, logout } from '@/api/user';

const state = {
	token: getAuthFromCookie() || '',
	admin: getAdminFromCookie() || '',
	name: getUserFromCookie() || '',
	id: getIdFromCookie() || '',
	identity: getIdentityFromCookie() || '',
	zipcode: 'asdf',
	street1: 'asdf',
};

const actions = {
	/* 	async FETCH_PARTNER_LIST(context) {
		let { data } = await Api.getPartnerList(process.env.VUE_APP_TITLE);
		context.commit('SET_PARTNER_LIST', data);
	}, */
	async LOGIN({ commit }, userInfo) {
		const result = await login(userInfo);
		if (result.data['jwt-token']) {
			commit('LOGIN', result.data);
			saveAuthToCookie(result.data['jwt-token']);
			saveIdToCookie(result.data.id);
			saveAdminToCookie(result.data.adminYn);
			saveIdentityToCookie(result.data.identity);
		} else {
			alert('    ');
		}
	},
	async LOGOUT({ commit }) {
		await logout();
		commit('LOGOUT');
		deleteCookie('til_auth');
		deleteCookie('til_user');
		deleteCookie('til_admin');
	},
};

const mutations = {
	LOGIN(state, data) {
		if (!data) return;
		state.token = data['jwt-token'];
		state.admin = data.adminYn;
		state.name = data.name;
		state.id = data.id;
	},
	LOGOUT(state) {
		state.token = null;
		state.admin = null;
		state.name = null;
	},

	SET_ADDRESS: function(state, payload) {
		state.zipcode = payload.zipcode;
		state.street1 = payload.roadAddress;
	},
};

const getters = {
	getId() {
		return state.id;
	},
	getToken() {
		return state.token;
	},
	getIdentity() {
		return state.identity;
	},
	getAdmin() {
		return state.admin;
	},
	getZipcode() {
		return state.zipcode;
	},
	getstreet1() {
		return state.street1;
	},
};

export default {
	state,
	actions,
	mutations,
	getters,
};
