// 

import { instance, instanceWithAuth } from './index';

function fetchBannerTotalCount() {
	return instance.get(`api/banner/totalCount`);
}

function fetchBanner() {
	return instance.get('api/banner/all');
}

function fetchBannerDetail(id) {
	return instance.get(`api/banner/detail/${id}`);
}

function addBanner(data) {
	return instanceWithAuth.post('api/admin/banner/new', data, {
		headers: { 'Content-Type': 'multipart/form-data' },
	});
}

function updateBanner(id, data) {
	return instanceWithAuth.put(`api/admin/banner/update/${id}`, data);
}

function deleteBanner(data) {
	return instanceWithAuth.delete('api/admin/banner/delete', { data });
}

export {
	fetchBannerTotalCount,
	fetchBanner,
	fetchBannerDetail,
	addBanner,
	updateBanner,
	deleteBanner,
};
