<template>
	<div class="sidemenu" :class="getIsNavOpen ? 'on' : ''">
		<div class="sidemenu-header">
			<Logo class="logo" />
			<span @click="clickMenu">
				<Close />
			</span>
		</div>
		<div class="sidemenu-body">
			<ul class="responsive-category">
				<li @click="goto('/about')">
					<span> </span>
				</li>
				<li @click="goto('/member')">
					<span> </span>
				</li>
				<li @click="goto('/program')"></li>
				<li @click="goto('/goods')"></li>
				<li @click="goto('/youtube')"> </li>
				<li @click="goto('/column')"> </li>
			</ul>

			<div v-if="getToken" class="btn-container">
				<!-- <a class="cart-btn" @click="goto('/cart')">
					
					<span class="count" @click="clickMenu">0</span>
				</a> -->
				<a @click="goto('/admin/rolling-banner')" v-if="getAdmin == 'Y'"
					></a
				>
				<a @click="goto('/mypage')"></a>
				<!-- <a @click="goto('/order')"></a> -->
				<!-- <a @click="goto('/review')"></a> -->
				<a @click="goto('/question')">1:1</a>
				<a @click="logout"></a>
			</div>
			<div v-else class="btn-container">
				<button v-if="false" class="login-btn" @click="goto('/login')">
					
				</button>
			</div>
		</div>
	</div>
</template>

<script>
import { mapGetters } from 'vuex';
import Close from '../assets/icon/Close.vue';
import Logo from '../assets/icon/Logo.vue';
export default {
	components: { Close, Logo },
	computed: {
		...mapGetters(['getIsNavOpen', 'getToken', 'getAdmin']),
	},
	data: () => ({
		//
	}),
	methods: {
		clickMenu() {
			this.$store.commit('toggleNav');
		},
		async logout() {
			await this.$store.dispatch('LOGOUT');
			this.$router.go();
			this.$store.commit('toggleNav');
		},
		goto(url) {
			this.$router.push({ path: url });
			this.$store.commit('toggleNav');
		},
	},
};
</script>

<style scoped lang="scss">
.sidemenu {
	position: fixed;
	top: 0;
	background: white;
	width: 100%;
	height: 100%;
	z-index: 9999;
	display: none;
	font-family: Black Han Sans;
}

.sidemenu.on {
	display: block;
}

.sidemenu-header {
	height: 106px;
	display: flex;
	justify-content: space-between;
	align-items: center;
}

.sidemenu-header span {
	margin-right: 20px;
}

.sidemenu-body {
	padding: 50px;
}

.responsive-category {
	margin-bottom: 20px;
}
.responsive-category li {
	//styleName: H3 - 24;
	font-family: Noto Sans KR;
	font-size: 24px;
	font-style: normal;
	font-weight: 900;
	line-height: 35px;
	text-align: left;
	margin-bottom: 16px;
}

.btn-container {
	display: block;
}

.btn-container a {
	display: block;
	background: none;
	font-family: Black Han Sans;
	font-size: 16px;
	line-height: 20px;
	display: block;
	margin-bottom: 20px;
}

@media screen and (min-width: 768px) {
	.sidemenu.on {
		display: none;
	}
}
</style>
