function saveAuthToCookie(value) {
	document.cookie = `til_auth=${value}`;
}

function saveUserToCookie(value) {
	document.cookie = `til_user=${value}`;
}

function saveAdminToCookie(value) {
	document.cookie = `til_admin=${value}`;
}

function saveIdToCookie(value) {
	document.cookie = `til_id=${value}`;
}

function saveIdentityToCookie(value) {
	document.cookie = `til_identity=${value}`;
}

function getAuthFromCookie() {
	return document.cookie.replace(
		/(?:(?:^|.*;\s*)til_auth\s*=\s*([^;]*).*$)|^.*$/,
		'$1',
	);
}

function getUserFromCookie() {
	return document.cookie.replace(
		/(?:(?:^|.*;\s*)til_user\s*=\s*([^;]*).*$)|^.*$/,
		'$1',
	);
}

function getAdminFromCookie() {
	return document.cookie.replace(
		/(?:(?:^|.*;\s*)til_admin\s*=\s*([^;]*).*$)|^.*$/,
		'$1',
	);
}

function getIdFromCookie() {
	return document.cookie.replace(
		/(?:(?:^|.*;\s*)til_id\s*=\s*([^;]*).*$)|^.*$/,
		'$1',
	);
}

function getIdentityFromCookie() {
	return document.cookie.replace(
		/(?:(?:^|.*;\s*)til_identity\s*=\s*([^;]*).*$)|^.*$/,
		'$1',
	);
}

function deleteCookie(value) {
	document.cookie = `${value}=; expires=Thu, 01 Jan 1970 00:00:01 GMT;`;
}

export {
	saveAuthToCookie,
	saveUserToCookie,
	saveAdminToCookie,
	saveIdToCookie,
	saveIdentityToCookie,
	getAuthFromCookie,
	getUserFromCookie,
	getAdminFromCookie,
	getIdFromCookie,
	getIdentityFromCookie,
	deleteCookie,
};
