'use strict';

const state = {
	main: [],
	detail: [],
	bi: [],
	web: [],
	mobile: [],
	image1: [],
	image2: [],
	image3: [],
};

const actions = {
	/* 	async FETCH_PARTNER_LIST(context) {
		let { data } = await Api.getPartnerList(process.env.VUE_APP_TITLE);
		context.commit('SET_FILE', data);
	}, */
};

const mutations = {
	SET_MAIN(state, data) {
		state.main = data;
	},
	SET_DETAIL(state, data) {
		state.detail = data;
	},
	SET_BI(state, data) {
		state.bi = data;
	},
	SET_WEB(state, data) {
		state.web = data;
	},
	SET_MOBILE(state, data) {
		state.mobile = data;
	},
	SET_IMAGE1(state, data) {
		state.image1 = data;
	},
	SET_IMAGE2(state, data) {
		state.image2 = data;
	},
	SET_IMAGE3(state, data) {
		state.image3 = data;
	},
};

const getters = {
	main() {
		return state.main;
	},
	detail() {
		return state.detail;
	},
	bi() {
		return state.bi;
	},
	web() {
		return state.web;
	},
	mobile() {
		return state.mobile;
	},
	image1() {
		return state.image1;
	},
	image2() {
		return state.image2;
	},
	image3() {
		return state.image3;
	},
};

export default {
	state,
	actions,
	mutations,
	getters,
};
