<template>
	<div class="main-swiper">
		<Swiper
			class="category-swiper"
			:options="swiperOption"
			ref="categorySlider"
		>
			<SwiperSlide
				class="carousel-item"
				v-for="item in bannerList"
				:key="item._id"
			>
				<div class="content-box">
					<div class="item-info">
						<h6 class="carousel-category">{{ item.categoryId }}</h6>
						<h1 class="carousel-title">{{ item.title }}</h1>
						<a :href="item.link" target="_blank"
							><button class="btn">{{ item.btnTitle }}</button></a
						>
					</div>
				</div>
				<img
					:src="item.imgPath"
					class="carousel-img"
					style="background: gray"
				/>
			</SwiperSlide>
		</Swiper>

		<!-- Add Pagination -->
		<div class="home-pagination"></div>
	</div>
</template>

<script>
import { fetchBanner } from '@/api/banner';
export default {
	created() {
		this.fetchBanner();
	},
	data() {
		return {
			bannerList: [],
			swiperOption: {
				slidesPerView: 1,
				slidesPerGroup: 1,
				navigation: {
					nextEl: '.category-button-next',
					prevEl: '.category-button-prev',
				},
			},
			pagination: {
				el: '.home-pagination',
				type: 'fraction',
			},
		};
	},
	methods: {
		async fetchBanner() {
			const { data } = await fetchBanner();
			this.bannerList = data;
		},
	},
};
</script>

