const state = {
	isNavOpen: false,
};

const actions = {
	/* 	async FETCH_PARTNER_LIST(context) {
		let { data } = await Api.getPartnerList(process.env.VUE_APP_TITLE);
		context.commit('SET_PARTNER_LIST', data);
	}, */
};

const mutations = {
	toggleNav(state) {
		state.isNavOpen = !state.isNavOpen;
	},
};

const getters = {
	getIsNavOpen: function(state) {
		return state.isNavOpen;
	},
};

export default {
	state,
	actions,
	mutations,
	getters,
};
